@import url('https://fonts.googleapis.com/css2?family=Alegreya:wght@700&display=swap');

html {
    margin: 0!important;
    width: 100vw;
    height: 100vh;
}

.App {
    margin: 0!important;
    height: 100vh;
    width: 100vw;
    text-align: center;
}

.App-name {
    pointer-events: none;
    color: white;
    background: url(https://upload.wikimedia.org/wikipedia/commons/0/00/Crab_Nebula.jpg) no-repeat;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

@media (prefers-reduced-motion: no-preference) {
    .App-name {
        animation: grow infinite 20s linear;
        animation-direction: alternate;
    }
}

.App-header {
    background-color: #282c34;
    min-height: 90%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 3.5vmin;
    color: white;
}

.App-footer {
    display: flex;
    min-height: 10%;
    width: 100%;
    justify-content: flex-end;
    align-items: center;
    color: #282c34;
    background-color: honeydew;
}

.App-link {
    color: #61dafb;
}

button {
    font-size: 3vmin;
    -webkit-appearance: none;
    border: 0;
    background: transparent;
}

button:hover {
    border-bottom: 3px solid #282c34;
}

@keyframes grow {
    from {
        transform: scale(1);
    }
    to {
        transform: scale(4.333);
    }
}
